#centerDiv {
    width: 75%;
    max-width: 40rem;
}

.teamImages {
    width: 60%;
}

@media only screen and (max-width: 480px) {
    /* For Mobile */
    #centerDiv {
        width: 90%;
    }

    #homeTitle {
        font-size: 32px;
    }

    .statNumber {
        font-size: 30px;
    }

    .statDesc {
        font-size: 14px;
    }

    .teamDiscord {
        font-size: 14px;
    }

    .teamImages {
        width: 50%;
    }
}